.contacts__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}

.contacts__input {
  background-color: @basicspace;
  height: 58px;
  border: none;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}

.contacts__input:hover {
  background-color: @extraspace;
}

.contacts__input:active {
  background-color: @white;
}

.contacts__group {
  border: none;
  border-bottom: 2px solid @extraspace;
}

.contacts__list {
  list-style: none;
}

.contacts__item {
  position: relative;
}

.contacts__item--name {
  margin-bottom: 20px;
}

.contacts__item--checkbox {
  margin-bottom: 54px;
}

.contacts__item--radio {
  margin-bottom: 43px;
}

.contacts__input--phone,
.contacts__input--mail {
  width: 280px;
}

.contacts__input::placeholder {
  color: @black;
}

.contacts__message::placeholder {
  color: @black;
}

.contacts__label--phone::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 58px;
  height: 58px;
  background-image: url("../img/group-phone.svg");
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.contacts__label--mail::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 58px;
  height: 58px;
  background-image: url("../img/group-mail.svg");
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.contacts__label {
  position: relative;
}

.contacts__label-radio {
  position: relative;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.contacts__label-checkbox {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.contacts__label-radio,
.contacts__label-checkbox {
  padding-left: 60px;
}

.contacts__input--radio::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 48px;
  width: 48px;
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.contacts__label--radio:checked::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 18px;
  width: 18px;
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.contacts__label-checkbox:checked::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 46px;
  width: 46px;
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.contacts__label-checkbox::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 46px;
  width: 46px;
  transform: translateY(-50%);
  background-repeat: no-repeat;
}

.contacts__message {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}

.contacts__button {
  font-family: "PT Sans", "Arial", sans-serif;
  width: 280px;
  font-size: 21px;
  line-height: 26px;
  height: 58px;
  padding: 32 px 0 32px 0;
  color: @white;
  background-color: @blue;
  border: none;
  text-transform: uppercase;
}

.contacts__button:hover {
  background-color: @extrablue;
}

.contacts__button:active {
  background-color: @basicblue;
  color: rgba(@white, 0.3);
}

.contacts__button:disabled {
  background-color: @basicwhite;
}

.contacts__module {
  display: none;
}

.contacts__module-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

.contacts__module-text {
  line-height: 24px;
  color: @space;
}

.contacts__module-button {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  height: 58px;
  padding: 32 px 0 32px 0;
  line-height: 26px;
  color: @white;
  width: 280px;
  background: @blue;
  border: none;
  text-transform: uppercase;
}
